@use "../index" as var;

.container {
    width: 300px;
    background-color: var.$secondary-color;
    display: block;
    border-radius: 20px;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    margin-top: 15px;
    filter: blur(10px);
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.5);
}

.app-title {
    width: 300px;
    height: 50px;
    border-radius: 10px 10px 0 0;

    p {
        text-align: center;
        padding: 15px;
        margin: 0 auto;
        font-size: 1.2em;
        color: var.$primary-color;
    }
}

.date-time {
    p {
        color: var.$primary-color;
        text-align: center;
        font-size: 0.8em;
    }
}

.notification {
    background-color: var.$accent-color;
    display: none;

    p {
        color: var.$error-color;
        font-size: 1.2em;
        margin: 0;
        text-align: center;
        padding: 10px;
    }
}

.weather-container {
    width: 300px;
    height: 260px;
    background-color: var.$secondary-color;
}

.weather-icon {
    width: 300px;
    height: 128px;

    img {
        display: block;
        margin: 0 auto;
        height: 100%;
        text-align: center;
    }
}

.temperature-value {
    width: 300px;
    height: 60px;

    p {
        padding: 0;
        margin: 0;
        color: var.$primary-color;
        font-size: 4em;
        text-align: center;
        cursor: pointer;
    }

    span {
        color: var.$primary-color;
        font-size: 0.5em;
    }
}

.data-big {
    p {
        padding: 8px;
        margin: 0;
        color: var.$primary-color;
        text-align: center;
        font-size: 1.2em;
    }
}

.data {
    p {
        margin: 0;
        padding: 0 10px;
        color: var.$primary-color;
        text-align: center;
        font-size: 0.8em;

        span {
            font-weight: bold;
        }
    }
}

.loader {
    position: absolute;
    left: 46.5%;
    top: 46.5%;
    border: 8px solid #F3F3F3;
    border-top: 8px solid #3498DB;
    border-radius: 50%;
    z-index: 2;
    width: 120px;
    height: 120px;
    animation: spin 1s linear infinite;
    transition: all 0.5s ease;
    transform: translate(-50%, -50%);
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}

.loader-wrapper {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    z-index: 2;
    width: 100%;
    height: 100%;
    transition: all 0.5s ease;

    &:before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.75);
    }

    p {
        color: var.$secondary-color;
        text-align: center;
        font-size: 1.2em;
        margin: 0;
        padding: 10px 0;
        position: absolute;
        left: 50%;
        top: 40%;
        transform: translate(-50%, -50%);
    }
}
