@use "../index" as var;

.modal-container {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.75);
    z-index: 2;
    justify-content: center;
    align-items: center;
    display: flex;
    transition: all 0.5s ease;
}

.modal-content {
    border-radius: 10px;
    box-shadow: 0px 0px 15px rgba(255, 255, 255, 0.25);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transition: all 0.5s ease;
}

.modal-title {
    padding: 0 150px;
    width: 100%;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var.$primary-color;
    border-radius: 10px 10px 0 0;
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.5);
    transition: all 0.5s ease;
    position: relative;

    p {
        color: var.$secondary-color;
        font-size: 1.2em;
        margin: 0;
        padding: 0;
        text-align: center;
    }

    .close-button {
        position: absolute;
        top: 0;
        right: 0;
        width: 30px;
        height: 30px;
        margin: 10px;
        background-color: var.$primary-color;
        border-radius: 50%;
        box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.5);
        cursor: pointer;
        display: flex;
        justify-content: center;
        align-items: center;
        border: none;
        transition: all 0.5s ease;

        span {
            color: var.$secondary-color;
        }

        &:hover,
        &:active {
            background-color: var.$secondary-color;

            span {
                color: var.$primary-color;
            }
        }
    }
}

.modal-body {
    width: 100%;
    height: 400px;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: var.$primary-color;
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.5);
    transition: all 0.5s ease;
}

.modal-footer {
    height: 50px;
    border-radius: 0 0 10px 10px;
    color: var.$secondary-color;
    font-size: 0.8em;

    p {
        padding: 0 10px;

        a {
            color: var.$secondary-color;
            transition: all 0.5s ease;

            &:hover {
                color: var.$accent-color;
            }
        }
    }
}

.modal-row {
    width: 100%;
    height: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    transition: all 0.5s ease;
}

.modal-label {
    width: 50%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var.$primary-color;
    transition: all 0.5s ease;

    p {
        color: var.$secondary-color;
        font-size: 1.2em;
        margin: 0;
        padding: 0;
        text-align: center;
    }
}

.modal-input {
    width: 50%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var.$secondary-color;
    transition: all 0.5s ease;

    select {
        width: 100%;
        height: 100%;
        border: none;
        background-color: var.$primary-color;
        color: var.$secondary-color;
        font-size: 1.2em;
        transition: all 0.5s ease;

        &:focus {
            outline: none;
        }

        &:hover {
            cursor: pointer;
        }
    }
}
